<template>
  <div class="modal-body">
    <div class="tab-container">
        <div class="tabs">
          <ul class="d-flex">
              <li class="active"><a v-on:click="activetab='1'" v-bind:class="[ activetab === '1' ? 'active' : '' ]"><span><img src="@/assets/images/icons/domestic.png" alt=""></span> Domestic</a></li>
              <li><a v-on:click="activetab='2'" v-bind:class="[ activetab === '2' ? 'active' : '' ]"><span><img src="@/assets/images/icons/imported.png" alt=""></span>Imported</a></li>
          </ul>
        </div>
        <div class="tab-content">
          <div v-if="activetab ==='1'" class="tabcontent">
            <ul class="reel-list" v-for="productName in productInfoDomestic" :key="productName">
              <li v-if="productName=='Alcohol'">
                <img src="@/assets/images/icons/alcohol-domestic-info.png" alt="">Alcohol</li>
              <li v-else-if="productName=='Tobacco'">
                <img src="@/assets/images/icons/tobacco-domestic-info.png" alt="">Tobacco</li>
              <li v-else-if="productName=='Beer'">
                <img src="@/assets/images/icons/beer-domestic-info.png" alt="">Beer</li>
              <li v-else-if="productName=='Soda'">
                <img src="@/assets/images/icons/soda-domestic.png" alt="">Soda</li>
              <li v-else-if="productName=='Water'">
                <img src="@/assets/images/icons/water-domestic-info.png" alt="">Water</li>
              <li v-else>
                <img src="@/assets/images/icons/water-domestic-info.png" alt="">{{productName}}</li>
            </ul>
          </div>
          <div v-if="activetab ==='2'" class="tabcontent">
            <ul class="reel-list" v-for="productName in productInfoImported" :key="productName">
              <li v-if="productName=='Alcohol'">
                <img src="@/assets/images/icons/alcohol-domestic-info.png" alt="">Alcohol</li>
              <li v-else-if="productName=='Tobacco'">
                <img src="@/assets/images/icons/tobacco-domestic-info.png" alt="">Tobacco</li>
              <li v-else-if="productName=='Beer'">
                <img src="@/assets/images/icons/beer-domestic-info.png" alt="">Beer</li>
              <li v-else-if="productName=='Soda'">
                <img src="@/assets/images/icons/soda-domestic.png" alt="">Soda</li>
              <li v-else-if="productName=='Water'">
                <img src="@/assets/images/icons/water-domestic-info.png" alt="">Water</li>
              <li v-else>
                <img src="@/assets/images/icons/water-domestic-info.png" alt="">{{productName}}</li>
            </ul>
          </div>
        </div>
    </div>
  </div>  
</template>

<script>
  export default {
    data: () => ({
      activetab: '1',
    }),
    props:{ productInfoDomestic:{default:[]},productInfoImported:{default:[]}}
  }
</script>
