<template>
    <div>
        <div class="d-block header-section">
            <div class="float-left"> <h1><b>Reel Management</b></h1></div>
            <div class="float-right"> <button type="button" class="btn btn-link" v-on:click="loadProductsforReelAdd()"><img src="@/assets/images/icons/add.png" alt="">NEW REEL SIZE</button></div>
        </div>
        <!-- Modal content Add-->
        <b-modal id="addNewReel" hide-footer centered>
            <template v-slot:modal-title>
                Add Reel Size
            </template>
            <div class="d-block">
                <form @keyup.13="addReelSize">
                    <div class="form-row">
                        <div class="form-check form-check-inline mb-2">
                            <label for="validationCustom01" class="label-coilsize p-2">Reel Size</label>
                            <div class="col-md-3">
                              <div class="form-group" :class="{ 'form-group--error': $v.coilsize.$error }">
                                        <input type="number" class="form-control" placeholder="Reel Size" v-model.number="$v.coilsize.$model" />
                                        <div v-if="addReelSizeErrors">
                                            <span class="text-danger" v-if="!$v.coilsize.required">Reel Size is required</span>
                                            <span class="text-danger" v-else-if="!$v.coilsize.checkNaN">Reel Size is required</span>
                                            <span class="text-danger" v-else-if="!$v.coilsize.checkValid">Reel Size should have above 2500 </span>
                                            <span class="text-danger" v-else-if="!$v.coilsize.checkDuplicateSize">Reel Size already exist</span>
                                        </div>
                                    </div>
                            </div>
                        </div>
                    </div>
                    <h5 class="w-50">Product(s)</h5>
                    <div class="form-row" v-for="(product) in arrProductsListforReelsAdd" :key="product.productId">
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtProdName_'+product.productId" name="example" v-model="product.isActive">
                                <label class="custom-control-label" :for="'swtProdName_'+product.productId">{{product.name}}<span v-if="product.origin==1"> Domestic</span><span v-else> Imported</span></label>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtMGF_'+product.productId" name="example" v-model="product.canMfOrder">
                                <label class="custom-control-label" :for="'swtMGF_'+product.productId">Visible to manufacturer</label>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtTPSAF_'+product.productId" name="example" v-model="product.canTpsafOrder">
                                <label class="custom-control-label" :for="'swtTPSAF_'+product.productId">Visible to TPSAF</label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-primary btn-lg" @click="$bvModal.hide('addNewReel')">Cancel</button>
                <button type="button" class="btn btn-lg btn-primary" v-on:click="addReelSize()">Submit</button>
            </div>
        </b-modal>
        <!-- Modal content End-->
        <!-- Modal content Edit-->
        <b-modal id="editReel" hide-footer hide-header-close centered>
            <template v-slot:modal-title>
                Edit Reel Management
            </template>
            <div class="d-block">
                <form class="needs-validation" novalidate>
                    <div class="form-row">
                        <div class="form-check form-check-inline mb-2">
                            <label for="validationCustom01" class="label-coilsize p-2">Reel Size</label>
                            <div class="col-md-3">
                                <input type="number" class="form-control" v-model.trim="$v.coilsize.$model" disabled>
                            </div>
                        </div>
                    </div>
                    <h5 class="w-50">Product(s)</h5>
                    <div class="form-row" v-for="(product) in arrProductsListforReelsEdit" :key="product.productId">
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtProdName_'+product.productId" name="example" v-model="product.isActive">
                                <label class="custom-control-label" :for="'swtProdName_'+product.productId">{{product.name}}<span v-if="product.origin==1"> Domestic</span><span v-else> Imported</span></label>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtMG_'+product.productId" name="example" v-model="product.canMfOrder">
                                <label class="custom-control-label" :for="'swtMG_'+product.productId">Visible to manufacturer</label>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtTPSAF_'+product.productId" name="example" v-model="product.canTpsafOrder">
                                <label class="custom-control-label" :for="'swtTPSAF_'+product.productId">Visible to TPSAF</label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-primary btn-lg" @click="warningMsg()">Cancel</button>
                <button type="button" class="btn btn-lg btn-primary" @click=" updateReelSize(); $bvModal.hide('editReel') ">Submit</button>
            </div>
        </b-modal>
        <!-- Modal content End-->
        <!-- Deactivate Model -->
        <b-modal id="deactiveItem" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">Add Reel Management</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="activeDeactiveReelStatus" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you Sure</h3>
                    <p v-if="activeDeactiveReelStatus" class="mb-5">you want to activate this Reel Size?</p>
                    <p v-else class="mb-5">you want to deactivate this Reel Size?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactiveItem')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activeDeactiveReelSize();">Ok</button>
                </div>
            </div>
        </b-modal>
        <!-- Deactivate Model End-->
		<div class="table-responsive">
        <table id="tab" class="table table-sm coilmanagement">
            <thead>
                <tr class="ro">
                    <th scope="col" class="coil-size">COIL SIZE</th>
                    <th scope="col" class="status">STATUS</th>
                    <th scope="col" class="actions wid-340">ACTIONS</th>
                </tr>
            </thead>
            <tbody>
                <tr class="ro" v-for="reels in getAllReelSizes" :key="reels.id">
                    <td class="al">{{reels.size}}</td>
                    <td class="cl" v-if="reels.isActive">Active</td>
                    <td class="text-danger" v-else>Inactive</td>
                    <td class="ad">
                        <div class="info-coil-container">
                            <b-dropdown id="info-coil-dropdown" ref="dropdown">
                                <template v-slot:button-content>
                                    <button type="button" class="btn btn-info btn-lg info" @click="getCoilInfo(reels.id)"><img src="@/assets/images/icons/info.png"> Info</button>
                                </template>
                                <div class="info-coil">
                                    <div class="right-arrow"></div>
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <h5 class="modal-title">Info</h5>
                                        </div>
                                        <CoilInfo :productInfoDomestic="objReelSizeInfoforDomestic" :productInfoImported="objReelSizeInfoforImported" />
                                    </div>
                                </div>
                            </b-dropdown>
                        </div>
                        <button type="button" class="btn btn-primary btn-lg editicon" v-on:click="loadProductsforReelEdit(reels.id,reels.size);"><img src="@/assets/images/icons/edit.png"> Edit</button>
                        <button type="button" class="btn btn-dark btn-lg deactivate" :class="{ btndeactivate : !reels.isActive }" v-on:click="getReelIdforActiveDeactive(reels.id, !(reels.isActive));">
                            <span v-if="reels.isActive"><img src="@/assets/images/icons/deactivate.png"> DEACTIVATE</span>
                            <span v-else><img src="@/assets/images/icons/deactivate.png"> ACTIVATE</span>
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
		</div>
    </div>
</template>

<script>
    import CoilInfo from "../popup/CoilInfo";
    import { required } from 'vuelidate/lib/validators';
    import toast from '@/toast';
    export default {
        data() {
            return {
                coilsize: NaN,addReelSizeErrors:false,activeDeactiveReelId: '',activeDeactiveReelStatus: true,
                objReelSizeInfoforDomestic: [],objReelSizeInfoforImported: [],arrProductsListforReelsEdit: [],arrProductsListforReelsAdd: [],ChangeTracker:false,reelSizeEditCounter:0
            }
        },
        mounted() {
            this.$store.dispatch("getAllReelSizesDtoAsync");
            this.$store.dispatch("getAllProductsDtoAsync");
        },
        computed: {
            getAllReelSizes() { return this.$store.getters.reelSizesDto; }
        },
        watch:{
            arrProductsListforReelsEdit:{
                deep:true,
                handler(){
                    this.reelSizeEditCounter+=1;
                    if(this.reelSizeEditCounter>1){
                    this.ChangeTracker=true;
                    }
                }
            }
        },
        validations: {
            coilsize: {
                required,
                checkNaN(value){
                    if(isNaN(value)) return false
                    return true;
                },
                checkValid(value) {
                    if (value < 2500) return false
                    return true;
                },
                checkDuplicateSize(value) {
                    if (this.$store.state.reelSizesDto.filter(element => element.size === value).length === 0) return true;
                    return false;
                }
            },
        },
        components: { CoilInfo},
        methods: {
            warningMsg(){
                if(this.ChangeTracker){
                if(confirm("Values are modified and not saved!!, If you click 'OK' then values won't be saved"))
                    this.$root.$emit('bv::hide::modal', 'editReel');
                }
                else
                   this.$root.$emit('bv::hide::modal', 'editReel');
            },
            getReelIdforActiveDeactive(id, activeStatus) {
                this.activeDeactiveReelId = id;
                this.activeDeactiveReelStatus = activeStatus;
                this.$root.$emit('bv::show::modal', 'deactiveItem');
            },
            activeDeactiveReelSize() {
                this.$store.dispatch("DeactivateReelSizeDtoAsync", [this.activeDeactiveReelId, this.activeDeactiveReelStatus]);
                this.$root.$emit('bv::hide::modal', 'deactiveItem');
            },
            getCoilInfo(reelSizeId) {
                this.objReelSizeInfoforDomestic = []; this.objReelSizeInfoforImported = [];
                let prodReelsById = this.$store.getters.reelSizesDto.filter((element) => element.productReelSizeDtos.
                    some((prodReel) => prodReel.reelSizeId === reelSizeId)).
                    map(element => { let reels = Object.assign([], element); return reels.productReelSizeDtos.filter(prodReel => prodReel.reelSizeId === reelSizeId); });
                if (prodReelsById.length !== 0) {
                    prodReelsById[0].filter(element=>element.isActive==1).forEach(prodReel => {
                        var product = this.$store.getters.productsDto.filter(element => element.id === prodReel.productId);
                        if (product[0].length !== 0) {
                            if (product[0].origin === 1)
                                this.objReelSizeInfoforDomestic.push(product[0].name);
                            else
                                this.objReelSizeInfoforImported.push(product[0].name);
                        }
                    });
                }
            },
            loadProductsforReelAdd() {
                this.coilsize = NaN;this.arrProductsListforReelsAdd = [];this.addReelSizeErrors=false;
                this.$store.getters.productsDto.forEach(element => {
                    var objProdReel = { id: 0, productId: element.id, name: element.name,origin:element.origin, reelSizeId: 0, size: 0, isActive: false, canMfOrder: false, canTpsafOrder: false };
                    this.arrProductsListforReelsAdd.push(objProdReel);
                });
                this.$root.$emit('bv::show::modal', 'addNewReel');
            },
            loadProductsforReelEdit(reelSizeId, reelSize) {
                this.ChangeTracker=false;this.reelSizeEditCounter=0;this.coilsize = reelSize;this.arrProductsListforReelsEdit = [];
                this.$store.getters.productsDto.forEach(element => {
                    var objProdReel = { id: 0, productId: element.id, name: element.name,origin:element.origin, reelSizeId: reelSizeId, size: reelSize, isActive: false, canMfOrder: false, canTpsafOrder: false };
                    this.arrProductsListforReelsEdit.push(objProdReel);
                });
                let prodReelsById = this.$store.getters.reelSizesDto.filter((element) => element.productReelSizeDtos.
                    some((prodReel) => prodReel.reelSizeId === reelSizeId)).
                    map(element => { let reels = Object.assign([], element); return reels.productReelSizeDtos.filter(prodReel => prodReel.reelSizeId === reelSizeId); });
                if (prodReelsById.length !== 0) {
                    this.arrProductsListforReelsEdit.forEach(reel1 => {
                        prodReelsById[0].forEach(reel2 => {
                            if (reel2.productId === reel1.productId) {
                                reel1.id = reel2.id; reel1.isActive = reel2.isActive; reel1.canMfOrder = reel2.canMfOrder; reel1.canTpsafOrder = reel2.canTpsafOrder;
                            }
                        });
                    });
                }
                this.$root.$emit('bv::show::modal', 'editReel');
            },
            async addReelSize() {
                this.$v.$touch();
                this.addReelSizeErrors=this.$v.coilsize.$anyError
                if (!this.addReelSizeErrors) {
                        let objReelSize = { id: 0, size: this.coilsize, isActive: true, productReelSizeDtos: [] };
                        this.arrProductsListforReelsAdd.forEach(element => {
                            if (element.isActive === true) { element.size = this.coilsize, delete element.name,element.origin; objReelSize.productReelSizeDtos.push(element); }
                        });
                        this.$root.$emit('bv::hide::modal', 'addNewReel');
                        await this.$store.dispatch("AddUpdateReelSizeDtoAsync", objReelSize);
                        await toast.success("Coil Added");
                    }
            },
            async updateReelSize() {
                let objReelSize = { id: 0, size: 0, isActive: true, productReelSizeDtos: [] };
                this.arrProductsListforReelsEdit.forEach(element => {
                    if (!(element.id === 0 && element.isActive === false)) { objReelSize.size = element.size; objReelSize.id = element.reelSizeId; delete element.name,element.origin; objReelSize.productReelSizeDtos.push(element); }
                });
                await this.$store.dispatch("AddUpdateReelSizeDtoAsync", objReelSize);
                await toast.success("Coil Updated");
            }
        }
    }
</script>
